.App {
  text-align: center;
}
.App-header {
  background-image: url('./images/bg-arlequin.jpg');
  background-position: center;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100vw;
  font-family: 'Shadows Into Light', cursive;
}
.bg-dark{
  background-color: rgb(7, 6, 6) !important;
}
.divLogo{
  width: 100px;
  height: 40px;
  background-image: url('./images/arlequin.png');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 120px;
}
.btn-outline-danger{
  border: 1px solid rgb(141, 4, 4)!important;
  color: rgb(141, 4, 4)!important;
  letter-spacing: 0.2em;
}
.btn-outline-danger:hover, .dropdown-item:active{
  background-color: rgb(141, 4, 4)!important;
  color:  rgb(7, 6, 6) !important;
}

.card{
  border: 4px solid rgb(65, 5, 5);
  border-radius: 8%;
  margin-left: 70%;
}